import React from 'react';
import Popup from 'reactjs-popup'
import 'reactjs-popup/dist/index.css'

function BossInfoItem(props) {
    return (
    <Popup className='popup_boss_info' trigger={<area target="" alt={props.area_alt} title={props.area_title} coords={props.area_coords} shape="circle"/>} modal>
      <div className='boss_info'>
          <img src={props.src} alt={props.alt} />
          <h1>{props.title}</h1>
          <p>{props.text}</p>
          <h3>Horarios: {props.time}</h3>
      </div>
    </Popup>
  );
}

export default BossInfoItem;