import React from 'react';
import { Link } from 'react-router-dom';

function CardItem(props) {

    const none = {
        display: 'none'
    }

    const grayfilter = {
        filter: 'grayscale(100%) blur(4px)'
    };

    return (
    <li className="cards_item" key={props.id}>
        {props.show ?
        <Link to={props.path}>
            <div className="card">
                <img className='card_background' src={props.src} alt={props.alt} />
                {props.logo ?
                <img className='lifeskill_logo' src={props.srclogo} alt={props.altlogo} />
                :
                <></>
                }
                <div className='card_content'>
                    <h1 className='card_title'>{props.title}</h1>
                    <p className="card_text">{props.text}</p>
                </div>
            </div>
        </Link>
        :
        <Link to={props.path} onClick={ (event) => event.preventDefault() }>
            <div className="card">
                <h2 className='title_prox'>PROXIMAMENTE</h2>
                <img className='card_background' src={props.src} alt={props.alt} style={grayfilter} />
                <div className='card_content' style={none}>
                    <h1 className='card_title'>{props.title}</h1>
                    <p className="card_text">{props.text}</p>
                </div>
            </div>
        </Link>
        }
    </li>
  );
}

export default CardItem;