import React from 'react';

function TitleItem(props) {

    let title;

    if(props.effect === 'oceano'){
        title = <h1 className='title_card'>{props.title}</h1>
    }else if(props.effect === 'night'){
        title = <h1 className='title_card_2'>{props.title}</h1>
    }else{
        title = <h1 className='title_card_3'>{props.title}</h1>
    }

    return (title);
}

export default TitleItem;