import React from 'react'
import './Cards.css'
import CardItem from '../../items/CardItem'
import LineItem from '../../items/LineItem'
import TitleItem from '../../items/TitleItem'

function Cards() {

    const list = [
        {
            id: 1,
            show: true,
            path:'/E404',
            src:'images/night/2020-06-29_1644308039.png',
            alt:'Card',
            title:'Night',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
    ];

    return (
        <>
        <div className='container_card'>
            <TitleItem effect='night' title='Night' />
            <LineItem />
            <div className='cards-container-oc' id='cards'>
                <ul className="cards">
                    {list.map(item => (
                        <CardItem
                            key={item.id}
                            show={item.show}
                            path={item.path}
                            src={item.src}
                            alt={item.alt}
                            title={item.title}
                            text={item.text}
                        />
                    ))}
                </ul>
            </div>
            <LineItem />
        </div>
        </>
    )
}

export default Cards
