import React from 'react'
import './Cards.css'
import CardItem from './items/CardItem'

function Cards() {

    const list = [
        {
            id: 1,
            show: true,
            path:'/Oceano',
            src:'images/cards/oceano.png',
            alt:'Card',
            title:'Oceano',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
        {
            id: 2,
            show: true,
            path:'/Night',
            src:'images/cards/night.jfif',
            alt:'Card',
            title:'Night',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
        {
            id: 3,
            show: true,
            path:'/Life',
            src:'images/cards/life.jpg',
            alt:'Card',
            title:'Life',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
        {
            id: 4,
            show: true,
            path:'/Bosses',
            src:'images/bosses/karanda.jpg',
            alt:'Card',
            title:'Bosses',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
        {
            id: 5,
            show: false,
            path:'/',
            src:'images/night/2020-06-29_1644308039.png',
            alt:'Card',
            title:'Black Market',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
    ];

    return (
        <>
        <div className='background-cards'>
            <img className='backgroundcards' src='images/cards/background.jpg' alt='Background' />
            <div className='titles-container'>
                <h1 className='title'>Informacion</h1>
                <div className="fade_line"></div> 
                <p className='text'>Lorem ipsum dolor sit amet, consectetur adipiscing elit</p>
            </div>
            <div className='cards-container' id='cards'>
                <ul className="cards">
                    {list.map(item => (
                        <CardItem
                            key={item.id}
                            show={item.show}
                            path={item.path}
                            src={item.src}
                            alt={item.alt}
                            title={item.title}
                            text={item.text}
                        />
                    ))}
                </ul>
            </div>
        </div>
        </>
    )
}

export default Cards
