import React from 'react'
import './Cards.css'
import CardItem from '../../items/CardItem'
import LineItem from '../../items/LineItem'
import TitleItem from '../../items/TitleItem'

function Cards() {

    const list = [
        {
            id: 1,
            show: true,
            path:'/Oceano/Khan',
            src:'images/oceano/khan.png',
            alt:'Card',
            title:'Khan',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
        {
            id: 2,
            show: true,
            path:'/Oceano/Intercambio',
            src:'images/oceano/bartering.jpg',
            alt:'Card',
            logo: true,
            srclogo:'images/life/iconos/lifeskill_riqueza.png',
            altlogo:'Badget',
            title:'Intercambio',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
        {
            id: 3,
            show: true,
            path:'/E404',
            src:'images/oceano/vell.jpg',
            alt:'Card',
            title:'Vell',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
        {
            id: 4,
            show: true,
            path:'/Oceano/Navegacion',
            src:'images/oceano/navegacion.jpg',
            alt:'Card',
            logo: true,
            srclogo:'images/life/iconos/lifeskill_navegacion.png',
            altlogo:'Badget',
            title:'Navegacion',
            text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do',
        },
    ];

    return (
        <>
        <div className='background-cards'>
            <img className='backgroundcards' src='images/oceano/worldmap.jpg' alt='Background' />
            <div className='container_card'>
                <TitleItem effect='oceano' title='Oceano' />
                <LineItem />
                <div className='cards-container-oc' id='cards'>
                    <ul className="cards">
                        {list.map(item => (
                            <CardItem
                                key={item.id}
                                show={item.show}
                                path={item.path}
                                src={item.src}
                                alt={item.alt}
                                logo={item.logo}
                                srclogo={item.srclogo}
                                altlogo={item.altlogo}
                                title={item.title}
                                text={item.text}
                            />
                        ))}
                    </ul>
                </div>
                <LineItem />
            </div>
        </div>
        </>
    )
}

export default Cards
